export class ParaphraseClient {
    public static async getParaphraseResults(text: string) : Promise<{ text: string }[]>{
        const response = await fetch("https://api.softcatala.org/paraphrase-service/v1/paraphrase", {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            mode: "cors" as RequestMode,
            body: JSON.stringify({ text }),
        });
        const data = await response.json() as ParaphraseResponse;

        const paraphrases = data
            .filter((entry) => entry.type === "paraphrase") // Filter out 'original' returned for reference
            .map((entry) => ({ text: entry.proposal }));

        return paraphrases;
    }

}

type ParaphraseResponse = (ParaphraseOriginal | ParaphraseProposal)[]

type ParaphraseOriginal = {
    type: 'original',
    proposal: string
}

type ParaphraseProposal = {
    type: 'paraphrase'
    proposal: string
}